<template>
  <div>
    <div class="container-fluid login-container">
      <div class="main-wrapper">
        <div class="welcome-container">
          <div class="welcome-wrapper mb-5 mx-auto">
            <!-- <h1 class="welcome-title mb-3 d-none" :class="{'d-block': !isForgot}">Welcome back!</h1> -->
            <h1 class="welcome-title mb-3 d-none" :class="{'d-block': isForgot}">Forget Your<br/>Password?</h1>
            <!-- <p class="welcome-text d-none" :class="{'d-block': !isForgot}">
              Please let this system help you get the job done more efficiently.
            </p> -->
            <p class="welcome-text d-none" :class="{'d-block': isForgot}">
              Enter your Username (email address) to reset your password. You may need to check your spam folder.
            </p>
          </div>
        </div>
        <div class="login">
          <div class="logo"></div>
          <form @submit.prevent class="login-wrapper d-none" :class="{'d-block': !isForgot}">
            <div class="form-group my-4">
              <label for="account">Account</label>
              <input class="form-control account-input" id="account" aria-describedby="Account" placeholder="Account">
            </div>
            <div class="form-group my-4">
              <label for="password">Password</label>
              <input type="password" class="form-control password-input" :class="{'outline-loginFail': isLogin}"
                id="password" placeholder="Password">
              <span class="show-password"></span>
              <small class="text-loginFail d-none" :class="{'d-inline-block mt-1': isLogin}">
                Passward error.
              </small>
            </div>
            <button type="submit" class="btn w-100 mt-3 btn-send" @click="getLoginData">
              LOGIN
            </button>
            <a href="#" @click.prevent="toggleForgot" class="forgot-password my-3">
              FORGET PASSWARD
            </a>
          </form>
          <form @submit.prevent class="login-wrapper d-none" :class="{'d-block': isForgot}">
            <div class="form-group mt-4 mb-2">
              <label for="account">Email</label>
              <input type="email" class="form-control account-input"
                id="email" aria-describedby="emailHelp" placeholder="Email">
            </div>
            <small class="text-SendEmail d-none" :class="{'d-inline-block mb-2': isSendEmail}">
              Email sent. Follow the instructions in the email.
            </small>
            <div class="btn-wrapper d-none" :class="{'d-block': !isSendEmail}">
              <button type="submit" class="btn w-100 mt-3 btn-send" @click="toggleSendEmail">
                SUBMIT
              </button>
              <button type="submit" class="btn w-100 mt-3 text-muted"
                :class="{'btn-transparent': isForgot}">
                CANCEL
              </button>
            </div>
            <button type="submit" class="btn w-100 mt-3 btn-send d-none" @click="backToLogin"
              :class="{'d-block': isSendEmail}">
              BACK TO LOGIN
            </button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../api';

export default {
  data() {
    return {
      isLogin: false, // 密碼是否錯誤
      isForgot: false, // 密碼是否忘記
      isSendEmail: false, // 重驗證信是否寄出
    };
  },
  methods: {
    toggleForgot() {
      this.isForgot = !this.isForgot;
    },
    toggleSendEmail() {
      this.isSendEmail = !this.isSendEmail;
    },
    backToLogin() {
      this.isLogin = false;
      this.isForgot = false;
      this.isSendEmail = false;
    },
    getLoginData() {
      const vm = this;
      const userID = document.getElementById('account').value;
      const password = document.getElementById('password').value;
      vm.$store.dispatch('loadingOverlay');
      return api.loginData(
        vm.$store.state.isCors, // 跨 http
        userID,
        password
      )
        .then((res) => res.json())
        .then((resjson) => {
          console.log(resjson);
          if (resjson.code === 200) {
            localStorage.setItem('token', JSON.stringify({ token: resjson.data }));
            localStorage.setItem('admin', JSON.stringify({ admin: userID }));
            vm.$store.dispatch('loadingOverlay');
            vm.$router.push({ path: 'dashboard' });
          } else {
            alert(`Error Code: ${resjson.code}, ${resjson.title}`);
            vm.$store.dispatch('loadingOverlay');
          }
        })
        .catch((err) => {
          alert(err);
          console.log(err);
          vm.$store.dispatch('loadingOverlay');
        })
    },
  }
};
</script>

